.App {
  width: 100vmin;
  height: 100vmin;
  margin: auto;
  display: grid;
  grid-template-columns: 40% 40%;
  grid-template-rows: 7% 70%;
  grid-template-areas:
    "_ nav"
    "Constructor Runtime";
  justify-content: space-evenly;
  align-content: space-evenly;
  font-size: 15vmin;
  .proba {
    background-color: aqua;
    width: 100px;
    height: 100px;
  }
}
